var self;
export default {

	data() {
		return {
			phoneNum: null,
			VerificationCode: null,
			VerificationMes: "获取验证码",
			disabled: false,
			loginInfo: Object,
			scope: 0
		}
	},
	onBackPress(options) {
		console.log('onback');
		uni.removeStorageSync('loginKey')
		uni.removeStorageSync('userId')
		return true
	},

	onLoad() {
		self = this;
		uni.getSetting({
			success(res) {
				console.log("授权：", res);
				if (!res.authSetting['scope.userInfo']) {
					//这里调用授权
					console.log("当前未授权");
					self.scope = 0
				} else {
					//用户已经授权过了
					console.log("当前已授权");
					self.scope = 1
				}
			}
		})
	},
	methods: {

		jumpToRegist() {
			uni.navigateTo({
				url: './regist',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},

		getVerificationCode() {
			if (!this.phoneNum || this.phoneNum.length !== 11) {
				uni.showToast({
					title: '请输入正确的手机号！'
				});
			} else {
				var ii = 0;
				this.disabled = true;
				this.VerificationMes = "60s";
				this.timer = setInterval(() => {

					ii = ii + 1;
					console.log("ii=" + ii);
					this.VerificationMes = (60 - ii).toString() + "s";

					if (ii == 30) {
						this.disabled = false;
						this.VerificationMes = "获取验证码";
						ii = 0;
						clearInterval(this.timer);
					}
				}, 1000)

				this.$api.post('login!ajaxGetPhoneCode.action', {
					tel: this.phoneNum
				}).then(res => {
					if (res.res.status == 0) {
						console.log("获取验证码成功")
					}
				})

			}

		},


		bindPhone() {

			var loginkey = uni.getStorageSync('loginKey');
			if (this.phoneNum.length == 11 && this.VerificationCode.length > 0) {
				// console.log("手机号"+this.phoneNum+"yzm"+this.VerificationCode)
				//注册成功 调用登录接口
				this.$api.post('user!ajaxFillInfo.action', {
					tel: this.phoneNum,
					phoneCode: this.VerificationCode,
					loginKey: loginkey,
					isApp: 0
				}).then(res => {
					if (res.res.status == 0) {
						var dict = res.inf;
						console.log(dict);
						uni.setStorageSync('userId', dict.userId);
						uni.showToast({
							title: '登录中···'
						});
						setTimeout(function() {
							uni.navigateBack({
								delta: 2
							})
						}, 1500)
					}
				})

			} else {
				uni.showToast({
					title: '请检查填写项！'
				});
			}


		},

		login() {},
		bindGetUserInfo() {
			let that = this
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log(loginRes);
					let code = loginRes.code;

					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							console.log(infoRes)
							console.log('用户昵称为：' + infoRes.userInfo.nickName);
							var data = {
								code: code,
								encryptedData: infoRes.encryptedData,
								iv: infoRes.iv,
								rawData: infoRes.rawData,
								signature: infoRes.signature,
							};

							that.$api.post('login!ajaxLoginByJSAPI.action', data).then(res => {
								if (res.res.status == 0) {
									console.log("loginres =" + res);
									// that.isLogin = true;
									// that.nickname = res.data.user.nickname
									// that.picture = res.data.user.picture
									// let pre_page = uni.getStorageSync('pre_page')
									// if (pre_page) {
									// 	uni.removeStorageSync('pre_page')
									// 	uni.navigateTo({
									// 		url: pre_page
									// 	})
									// }

								}

							})

						}
					})
				}
			})
		},

	}
}
